import uniListItem from '@/components/uni-ui/uni-list-item/uni-list-item.vue';
export default {
	components:{
		uniListItem
	},
	data() {
		return {
			currentSize:0
		}
	},
	onLoad() {
		this.getStorageInfo()
		
	},
	filters: {
		format: function(value) {
			return value > 1024 ? (value/1024).toFixed(2) + 'MB' : value.toFixed(2) + 'KB';
		}
	},
	methods: {
		getStorageInfo(){
			let res = uni.getStorageInfoSync()
			this.currentSize = res.currentSize
		},
		open(path){
			uni.navigateTo({
				url: `../${path}/${path}`,
			});
		},
		// 清除缓存
		clear(){
			uni.showModal({
				title: '提示',
				content: '是否要清除所有缓存',
				cancelText: '不清除',
				confirmText: '清除',
				success: res => {
					if (res.confirm) {
						uni.clearStorageSync()
						this.getStorageInfo()
						uni.showToast({
							title: '清除成功',
							icon: 'none'
						});
					}
				},
			});
		}
	}
}
